<template>
  <div class="wrapperCenter">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>来粉统计 / 控制台 / 项目设置 / 网址构建器</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="formBox">
    <el-form ref="form" :model="form" label-width="86px" class="from">
      <el-form-item label="落地页链接">
        <el-input v-model="form.url" type="url"></el-input>
      </el-form-item>
      <el-form-item label="投放渠道">
        <el-input v-model="form.channel"></el-input>
      </el-form-item>
      <el-form-item label="媒介类型">
        <el-input v-model="form.type"></el-input>
      </el-form-item>
      <el-form-item label="计划名称">
        <el-input v-model="form.planName"></el-input>
      </el-form-item>
      <el-form-item label="单元名称">
        <el-input v-model="form.unitName"></el-input>
      </el-form-item>
    </el-form>
    <el-button class="res" type="primary" style="text-align: center" @click="spliceUrl">生成</el-button>
    </div>
    <div>
      <p class="result">生成结果</p>
      <el-input
        type="textarea"
        :autosize="{ minRows: 2, maxRows: 4}"
        placeholder="请输入内容"
        v-model="textarea2"
        id="codeJS"
      ></el-input>
      <div class="btnBox">
        <el-button type="primary" @click="copy(search)">点击复制</el-button>
        <el-button type="reset" @click="reset">重置</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        url: "",
        channel: "",
        type: "",
        planName: "",
        unitName: "",
      },
      textarea2: "",
    };
  },
  watch: {
    form: {
      deep: true,
      handler() {
        // this.textarea2 =
        //   this.form.url +
        //   "?utm_source=" +
        //   this.form.channel +
        //   "&utm_medium=" +
        //   this.form.type +
        //   "&utm_term=" +
        //   this.form.planName +
        //   "&utm_plan=" +
        //   this.form.unitName;
      },
    },
  },
  methods: {
    spliceUrl() {
      if (this.form.url == "") {
        this.$message.error("请填写url");
        return;
      }
      let str = this.form.url + "?";
      if (this.form.channel != "") {
        str += "utm_source=" + this.form.channel + "&";
      }
      if(this.form.type != ""){
        str += "utm_medium=" + this.form.type + "&";
      }

      if(this.form.planName != ""){
        str += "utm_term=" + this.form.planName + "&";
      }
      if(this.form.unitName != ""){
        str += "utm_plan=" + this.form.unitName + "&";
      }
      // this.textarea2.replace(/\&$/g, "")
      const a = str.substr(0, str.length - 1);  
      
      this.textarea2 = a;
    },
    copy() {
      if(this.textarea2 == "") return;
      var codeJS = document.getElementById("codeJS");
      codeJS.select(); // 选择对象
      document.execCommand("Copy"); // 执行浏览器复制命令
      this.$message.success("复制成功");
    },
    reset() {
      this.form = {
        url: "",
        channel: "",
        type: "",
        planName: "",
        unitName: "",
      };
    },
  },
};
</script>

<style lang="less" scoped>
.formBox{
  
  border-radius: 6px;
  border: dashed 1px #ccc;
  padding: 50px 0;
.from {
  width: 50%;
  margin-top: 20px;
  margin: 0 auto;
  
}
}
.res {
      text-align: center;
    margin: 0 auto;
    position: relative;
    left: 50%;
}
.result {
  margin: 14px 0 16px 12px;
}
.btnBox {
  margin-top: 20px;
}
</style>